    import uniPopup from '../../components/uni-popup/uni-popup.vue'

    export default {
        onLoad(e) {
			uni.getStorage({
				key:'model',
				success:res=>{
					this.model = res.data
				}
			})
			
			// uni.request({
			// 	url:'https://www.finetwm.com/xmRepair/faults/getDetailList',
			// 	success: res=>{
			// 		this.faulesIdList = res.data.data
			// 	}
			// })
            //判断是否快捷维修入口
            let sid = e.id
            if(sid == ''){
                return
            }else{
                this.$refs.popup.open()
                let xtypeId = this.faulesList[sid].type
                uni.request({
                    url:'https://www.finetwm.com/xmRepair/faults/getDetail',
                    method:'POST',
                    data:{
                        type:xtypeId
                    },
                    success: res=>{
                        this.faulesItem = res.data.data
                    }
                })
            }
        },
        data() {
            return {
				model:'',
				faulesPrice:[],
				faulesTitle:[],
				faulesIdList:[],
				flist1:[],
                flist:[],
                faulesItemPrice:[],
                currentPrice:0,
                totalPrice: 0,
                isSelectedId: '',
                isSelected: false,
                unSelectedImg: '../../static/faults/wxz@2x.png',
                selectedImg: '../../static/faults/wxz@2x.png',
                faulesItem: [],
                faulesList: [{
                    imgSrc: '../../static/faults/screen@2x.png',
                    text: '屏幕问题',
                    type: "screen"
                }, {
                    imgSrc: '../../static/faults/battery@2x.png',
                    text: '电池电源问题',
                    type: "battery"
                }, {
                    imgSrc: '../../static/faults/signal@2x.png',
                    text: '连接/信号问题',
                    type: "signal"
                }, {
                    imgSrc: '../../static/faults/camare@2x.png',
                    text: '摄像头问题',
                    type: "camera"
                }, {
                    imgSrc: '../../static/faults/voice@2x.png',
                    text: '声音问题',
                    type: "voice"
                }, {
                    imgSrc: '../../static/faults/botton@2x.png',
                    text: '按键问题',
                    type: "keyboard"
                }, {
                    imgSrc: '../../static/faults/shield@2x.png',
                    text: '外壳边框问题',
                    type: "edging"
                }, {
                    imgSrc: '../../static/faults/board@2x.png',
                    text: '主板维修',
                    type: "mainboard"
                }, {
                    imgSrc: '../../static/faults/switchoff@2x.png',
                    text: '开关机问题',
                    type: "switch"
                }, {
                    imgSrc: '../../static/faults/unkeep@2x.png',
                    text: '保养服务',
                    type: "maintain"
                }, {
                    imgSrc: '../../static/faults/install@2x.png',
                    text: '安装服务',
                    type: "install"
                }, {
                    imgSrc: '../../static/faults/other@2x.png',
                    text: '其他故障',
                    type: "others"
                }]
            }
        },
        methods: {
			changeType(){
				uni.navigateTo({
					url: '../selectmodel/selectmodel'
				})
			},
            onSelected(e) {
                this.isSelectedId = e.currentTarget.id
            },
            openPopup(sid) {
                this.$refs.popup.open()
                let xtypeId = this.faulesList[sid].type
                uni.request({
                    url:'https://www.finetwm.com/xmRepair/faults/getDetail',
                    method:'POST',
                    data:{
                        type:xtypeId
                    },
                    success: res=>{
                        this.faulesItem = res.data.data
                    }
                })

            },
            closePopup() {
                this.$refs.popup.close()
            },
            onsiteRepair(e) {

                uni.navigateTo({
                    url: '../onsiterepair/onsiterepair',
					success:res=>{
						uni.removeStorage({
						    key: 'sid',
						    success:res => {
						        console.log('clear success')
						    }
						})
					}
                })
            },
            onsiteArrival(){
                uni.navigateTo({
                    url:'../arrival-repair/arrival-repair',
					success:res=>{
						uni.removeStorage({
						    key: 'sid',
						    success:res => {
						        console.log('clear success')
						    }
						})
					}
                })
            },
            onEventChange(e){
				let ind = e.detail.value
				let s= 0
				let t = []
				let sin = []
				for (let i = 0; i < ind.length; i++) {
					let price = this.faulesItem[ind[i]].price
					let title = this.faulesItem[ind[i]].title
					sin.push(price)
					t.push(title)
					s = s+ price
					this.totalPrice = s
					uni.setStorage({
						key:'faulesTitle',
						data:t
					})
					uni.setStorage({
						key:'totalPrice',
						data:this.totalPrice
					})
					uni.setStorage({
						key:'sprice',
						data:sin
					})
				}
				// console.log(this.faulesPrice)
			}



        },
        components: {
            uniPopup
        }
    }
